import { Component, OnInit } from '@angular/core';
import { Router, NavigationEnd, NavigationStart } from '../../../../../node_modules/@angular/router';
import { Location } from '../../../../../node_modules/@angular/common';
import { Helper } from '../../../modules/Helper';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {

  leavePage: boolean = false;
  webapplication: boolean = false;
  history: string[] = [];
  sent: boolean = false;

  constructor(private router: Router, private location: Location) {}

  ngOnInit() {
    //this.location.back();
    this.router.events.subscribe(event => {
      if(this.leavePage) {
        window.history.back();
      }
      if (event instanceof NavigationEnd) console.log("History", this.history);
      if (event instanceof NavigationStart) {
        //if (this.history.length == 0 || this.history.length == 1) {
        //  //for(var i = 0; i < 10; i++) this.location.back();
        //}
        //else {
          if (event.navigationTrigger == "popstate") {
            console.log("History", this.history);
            this.sent = true;
            if (this.history.length == ["/"].length) {
              this.leavePage = true;
            }
            else {
              console.log("SENDING TO", this.history.length, this.history[this.history.length - 2]);
              Helper.navigate(this.router, this.location, this.history[this.history.length - 2]);
              var tempHistory = [];
              for(var i = 0; i < this.history.length - 1; i++) {
                tempHistory.push(this.history[i]);
              }
              this.history = tempHistory;
              console.log(this.history);
            }
          }
        //}
      }
      else if (event instanceof NavigationEnd) {
        if (!this.sent) {
          if (!event.url.toLocaleLowerCase().includes("not-found")) {
            if (event.url !== this.history[this.history.length - 1])
              if (this.history[this.history.length - 1] === "/" && event.url === "/home") {}
              else {
                this.history.push(event.url);
              }
          }
        }
        else {
          this.sent = false;
        }
      }
    });
  
  }
}
