import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Location } from '@angular/common';
import { Helper, PageNames } from '../../../modules/Helper';
import { DB } from '../../../modules/DB';

class Page {
  name: string;
  apps: App[];
}

class App {
  name: string;
  file: string;
  description: string;
}

@Component({
  selector: 'app-applications',
  templateUrl: './applications.component.html',
  styleUrls: ['./applications.component.css']
})
export class ApplicationsComponent implements OnInit {

  closeWebApp() {
    this.webAppOpen = false;
  }

  webAppOpen: boolean = false;
  webAppURL: string = "";
  webAppName: string = "";
  webAppDescription: string = "";
  subpage: string = "";
  apps: App[] = [];
  subpages: Page[] = [];
  JavaApps: App[] = [];
  WebApps: App[] = [];
  AndroidApps: App[] = [];
  pageName: string;

  constructor(private router: Router, private location: Location/*, private http: HttpClient*/) { }

  ngOnInit() {
    Helper.initializePage(this, this.router.url, PageNames.APPLICATIONS);
    // retrieve info from db
    var db = DB.getDB();
    this.subpages = [];
    var subpages = db.nate314.home.pages[0]["subpages"];
    for(var i = 0; i < subpages.length; i++) {
      this.subpages.push(<Page>{ name: subpages[i].name, apps: subpages[i]["apps"] });
    }
    this.webAppOpen = false;
    this.webAppURL = this.router["currentUrlTree"].queryParams.appURL;
    this.webAppName = this.router["currentUrlTree"].queryParams.appName;
    this.webAppDescription = this.router["currentUrlTree"].queryParams.appDescription;
    this.subpage = this.router["currentUrlTree"].queryParams.subpage;
    if(this.subpage != undefined) {
      this.subpages.forEach(page => {
        if(page.name.toUpperCase().includes(this.subpage.toUpperCase())) {
          this.pageName = page.name;
          this.apps = page.apps;
        }
      });
    }
    // if a web app is being passed through the url, then open that web app
    if(!Helper.equalsNull(this.webAppName)) {
      //this.openWebAppURL(this.webAppName, this.webAppURL, this.webAppDescription);
      this.openWebAppURL(this.webAppName);
    }
  }

  openWebAppURL(_name: string) {
    this.openWebApp(<App>{ name: _name });
  }

  openWebApp(app: App) {
    // find app by name
    for (var i = 0; i < this.apps.length; i++)
      if(this.apps[i].name.toUpperCase() == app.name.toUpperCase())
        app = this.apps[i];
    // set url
    var iFrameURL = app.file;
    if(!iFrameURL.includes("https://nate314.github.io/"))
      iFrameURL = "https://nate314.github.io/" + app.file;
    // send to app
    if(Helper.equalsNull(this.webAppName))
      Helper.navigateTo(this.router, this.location, ["/applications"], { queryParams: { subpage: "web", appName: app.name }});
    // open app
    this.webAppURL = iFrameURL;
    this.webAppOpen = true;
    var iframe = `<iframe src="${iFrameURL}" frameborder="0" style="width:100%; height:60vh;"></iframe>`;
    document.getElementById("appContent").innerHTML = iframe;
    document.getElementById("appDescription").innerHTML = app.description;
  }

  openJavaApp(app: App) {
    var url = app.file;
    if(!url.includes("https://nate314.github.io/nathangawith/applications/javaApplications/"))
      url = "https://nate314.github.io/nathangawith/applications/javaApplications/" + app.file;
    location.href = url;
  }

  openAndroidApp(app: App) {
    var url = app.file;
    if(!url.includes("https://nate314.github.io/nathangawith/applications/androidApplications/"))
      url = "https://nate314.github.io/nathangawith/applications/androidApplications/" + app.file;
    location.href = url;
  }

  openSubPage(_subpage: string) {
    Helper.navigateTo(this.router, this.location, ["/applications"], { queryParams: { subpage: _subpage }});
  }
}
