import { GradeCalculatorComponent } from './components/pages/applications/grade-calculator/grade-calculator.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { NotFoundComponent } from './components/application-structure/not-found/not-found.component';
import { HomeComponent } from './components/pages/home/home.component';
import { ApplicationsComponent } from './components/pages/applications/applications.component';
import { VideosComponent } from './components/pages/videos/videos.component';
import { MultiplicationTableComponent } from './components/pages/applications/multiplication-table/multiplication-table.component';
import { NavigationComponent } from './components/application-structure/navigation/navigation.component';
import { FinalGradeCalculatorComponent } from './components/pages/applications/final-grade-calculator/final-grade-calculator.component';
import { BettingCalculatorComponent } from './components/pages/applications/betting-calculator/betting-calculator.component';
import { ColorFluxComponent } from './components/pages/applications/color-flux/color-flux.component';
import { FlappyFinchComponent } from './components/pages/applications/flappy-finch/flappy-finch.component';
import { FloatyStarsComponent } from './components/pages/applications/floaty-stars/floaty-stars.component';
import { GroupCreatorComponent } from './components/pages/applications/group-creator/group-creator.component';
import { HtmlSandboxComponent } from './components/pages/applications/html-sandbox/html-sandbox.component';
import { Say2Component } from './components/pages/applications/say2/say2.component';

const routes: Routes = [
  { path: '', redirectTo: '/home', pathMatch: 'full' },
  { path: 'home', component: HomeComponent },
  { path: 'applications', component: ApplicationsComponent },
  { path: 'videos', component: VideosComponent },
  { path: 'applications/java', redirectTo: '/applications?subpage=java', pathMatch: 'full' },
  { path: 'applications/web', redirectTo: '/applications?subpage=web', pathMatch: 'full' },
  { path: 'applications/android', redirectTo: '/applications?subpage=android', pathMatch: 'full' },
  { path: 'applications/web/:application', component: NavigationComponent},
  { path: 'webapplications/bettingcalculator', component: BettingCalculatorComponent },
  { path: 'webapplications/colorflux', component: ColorFluxComponent },
  { path: 'webapplications/finalgradecalculator', component: FinalGradeCalculatorComponent },
  { path: 'webapplications/flappyfinch', component: FlappyFinchComponent },
  { path: 'webapplications/floatystars', component: FloatyStarsComponent },
  { path: 'webapplications/gradecalculator', component: GradeCalculatorComponent },
  { path: 'webapplications/groupcreator', component: GroupCreatorComponent },
  { path: 'webapplications/htmlsandbox', component: HtmlSandboxComponent },
  { path: 'webapplications/multiplicationtable', component: MultiplicationTableComponent },
  { path: 'webapplications/say2', component: Say2Component },
  { path: '**', component: NotFoundComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
